import React from 'react'
import { Container, Card, Button } from 'reactstrap'
import NavbarContent from '../components/navigation/navbar'
import { PageHeadline } from '../components/headline'
import id8logo from '../images/logos/id8logo.png'
import Img from 'gatsby-image'
import styles from './blog.module.css'
import { Helmet } from 'react-helmet'
import ArticlePreview from '../components/article-preview/article-preview'

const OurTeam = (props) => {
    const team = props.data.allContentfulPerson.edges

    return (
        <React.Fragment>
            <Helmet title={'iD8 - Team'} />
            <NavbarContent />
            <div className={styles.hero}>Our Team</div>
            <div className="wrapper">
                <h2 className="section-headline">Current Leadership</h2>
                <ul className="article-list">
                    {team.map(({ node }) => {
                        return (
                            <li key={node.username}>
                                <ArticlePreview
                                    category="team"
                                    article={node}
                                />
                            </li>
                        )
                    })}
                </ul>
            </div>
        </React.Fragment>
    )
}

export const query = graphql`
    {
        allContentfulPerson {
            edges {
                node {
                    username
                    title
                    name
                    company
                    image {
                        fluid(
                            maxWidth: 350
                            maxHeight: 196
                            resizingBehavior: SCALE
                        ) {
                            ...GatsbyContentfulFluid_tracedSVG
                        }
                    }
                }
            }
        }
    }
`

export default OurTeam
